import { weatherActionTypes } from '../sagas/actionTypes';

export const defaultCenter = { lat: 55.752, lng: 37.615 };
const initialState = defaultCenter;


export default function positionReducer(state = initialState, action) {
  switch (action.type) {
    case weatherActionTypes.FETCH_WEATHER:
      return action.payload;

    case weatherActionTypes.FETCH_WEATHER_COMPLETE: {
      const { coord } = action.payload.city;
      return { lat: coord.lat, lng: coord.lon };
    }

    default:
      return state;
  }
}



